.task-bar-bckg.medium {
  width: 100%;
  height: 100%;
  background-color: var(--brightest);
}
.task-bar-bckg {
  width: 100%;
  height: 100%;
  background-color: none;
}
.task-bar {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.task-bar-content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.task-bar-time {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.time-stamp {
  font-family: "Tomorrow-Regular", "Courier New", sans-serif;
  font-size: 1.1rem;
  color: var(--brightest);
  background: none;
  border: none;
}
.task-bar-name {
  font-family: "Tomorrow-Regular", "Courier New", sans-serif;
  font-size: 1.2rem;
  width: 17rem;
  overflow-x: scroll;
  color: var(--brightest);
}
.task-item-control {
  width: 25%;
  height: 3rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.task-item-control li {
  background-repeat: no-repeat;
  background-size: cover;
  width: 1.5rem;
  height: 1.5rem;
}
.description-btn {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}
.edit {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_12___});
}
.delete-button {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_13___});
  background-size: 1.6rem 1.6rem;
}
.done {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_14___});
}
.marked {
  scale: 1;
}
.marked::before {
  content: "";
  rotate: -45deg;
  position: absolute;
  border: 0.3rem solid var(--brightest);
  left: 0.5rem;
  top: 0.5rem;
}
.dot {
  width: 0.2rem;
  height: 0.2rem;
  background-color: var(--brightest);
  border-radius: 0.1rem;
}

.description-box {
  visibility: hidden;
  background-color: var(--brightest);

  width: 100%;

  height: 0rem;
  transform: translateY(0);
}
.description-box.visible {
  visibility: visible;
  background-color: var(--brightest);

  width: 100%;
  min-height: 3.5rem;
  overflow-y: scroll;
  transform: translateY(0);
  border: 0.25rem solid var(--bluish-5);
  border-top: none;
}
.disable {
  pointer-events: none;
}
