--------REM---------------- */\nhtml {\n  font-size: 16px;\n}\n\n/* ============COLORs========== */\n:root {\n  --brightest: #e5feff; /* 20% */\n  --darkest: #10313c;\n  --bluish-1: #7da1a3;\n  --bluish-2: hsla(182, 24%, 59%, 0.31) /*31%*/;\n  --bluish-3: #53797b;\n  --bluish-4: #1e8f94;\n  --bluish-5: #00edfc;\n  --bluish-6: #7bd0d4;\n  --reddish: #ff0100;\n  --cell-1: #caf2f4;\n  --cell-2: #a0e1e4;\n  --cell-3: #d8f8f9;\n  --cell-4: #c4f0f1;\n\n  --blur: blur(2px);\n}\n/* ==================DARK MODE========================== */\n\n.dark-mode {\n  --brightest: #0000009a;\n  --cell-1: #7f9899;\n  --cell-2: #648d8f;\n  --cell-3: #90a6a7;\n  --cell-4: #718d8d;\n}\n\n/* ========STYLING=================STYLING============== */\n\n.button {\n  user-select: none;\n  cursor: pointer;\n}\n.push-button:hover {\n  scale: 1.1;\n}\n.push-button:active {\n  scale: 0.9;\n}\n.hidden {\n  display: none;\n}\n/* ---------BACKGROUND---------------------- */\nbody {\n  background-color: var(--darkest);\n  background-image: url(\"../assets/images/proper_tile.webp\");\n  background-size: 150px 150px;\n  background-repeat: repeat;\n}\n/* ------------MAIN SCAFFOLDING------------------*/\n#wrapper {\n  /* background-color: orange; */\n  max-width: 1780px;\n  height: 100vh;\n\n  display: flex;\n  justify-content: center;\n}\nheader {\n  /* background-color: greenyellow; */\n  width: 100%;\n  display: flex;\n  justify-content: start;\n  bottom: 2rem;\n  position: relative;\n}\n\n#content {\n  /* background-color: orange; */\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n  align-items: start;\n}\n\n#logo {\n  width: 17.5rem;\n  height: auto;\n}\n/* ----------------CALENDAR---------------- */\n\n/* -----------------COUNT BUTTON------------------- */\n#count-button {\n  width: 4rem;\n  height: 4rem;\n  background-color: transparent;\n\n  border-top: 2rem solid hsla(0, 100%, 50%, 0.7);\n  border-left: 2rem solid transparent;\n  border-bottom: 2rem solid transparent;\n  border-right: 2rem solid hsla(0, 100%, 50%, 0.7);\n  z-index: 2;\n  position: absolute;\n  top: 0;\n  right: 0;\n}\n#count-button-number {\n  position: absolute;\n  top: -1.8rem;\n  right: -1.25rem;\n  font-family: \"Tomorrow-Bold\", \"Courier New\", sans-serif;\n  font-size: 1.5rem;\n  color: var(--brightest);\n  -webkit-transform: rotate(45deg);\n  -moz-transform: rotate(45deg);\n  -o-transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n}\n\n/* -------SCROLL BUTTONS------------------------- */\n.scroll-button {\n  width: 2.5rem;\n  height: 10.75rem;\n}\n\n#scroll-left {\n  margin: auto 1rem auto 0;\n  background-color: transparent;\n  height: 10.75rem;\n  border-top: 5.375rem solid transparent;\n  border-left: 0rem solid transparent;\n  border-bottom: 5.375rem solid transparent;\n  border-right: 2.5rem solid var(--bluish-2);\n}\n#scroll-left:hover {\n  border-right: 2.5rem solid var(--bluish-6);\n}\n.scroll-button:active {\n  scale: 0.8;\n}\n\n#scroll-right {\n  margin: auto 0 auto 1rem;\n  background-color: transparent;\n  height: 10.75rem;\n  border-top: 5.375rem solid transparent;\n  border-right: 0rem solid transparent;\n  border-bottom: 5.375rem solid transparent;\n  border-left: 2.5rem solid var(--bluish-2);\n}\n#scroll-right:hover {\n  border-left: 2.5rem solid var(--bluish-6);\n}\n\n/* --------------DATE INPUT---------------------- */\n#calendar-date-form {\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  position: relative;\n  top: 1rem;\n}\n\n.custom-go-button {\n  width: 2.2rem;\n  height: 2.2rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--reddish);\n  -webkit-transform: rotate(45deg);\n  -moz-transform: rotate(45deg);\n  -o-transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n  box-shadow: 0rem 0rem 0rem 0.3rem var(--darkest);\n}\n#go-date-button:hover {\n  background-color: var(--reddish);\n  -webkit-transform: rotate(90deg);\n  -moz-transform: rotate(90deg);\n  -o-transform: rotate(90deg);\n  -ms-transform: rotate(90deg);\n  #go-text {\n    -webkit-transform: rotate(-90deg);\n    -moz-transform: rotate(-90deg);\n    -o-transform: rotate(-90deg);\n    -ms-transform: rotate(-90deg);\n  }\n}\n#go-date-button:active {\n  scale: 80%;\n}\n#go-date-button {\n  position: relative;\n  top: 0.55rem;\n  left: -1.2rem;\n}\n#go-text {\n  font-family: \"Tomorrow-Medium\", \"Courier New\", sans-serif;\n  font-size: 1.3rem;\n  color: var(--brightest);\n  -webkit-transform: rotate(-45deg);\n  -moz-transform: rotate(-45deg);\n  -o-transform: rotate(-45deg);\n  -ms-transform: rotate(-45deg);\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n}\n.date-input {\n  font-family: \"Tomorrow-Regular\", \"Courier New\", sans-serif;\n  color: var(--bluish-3);\n  font-size: 1.5rem;\n  position: absolute;\n  top: 0.6rem;\n  right: 23.6rem;\n  background: none;\n}\n.date-input:hover {\n  box-shadow: inset 0 2px 4px var(--bluish-1);\n  cursor: text;\n}\n\n#custom-date-input {\n  width: 15rem;\n}\n/* ================TASK MANAGER========================= */\n#tasks {\n  /* background-color: violet; */\n  top: 2rem;\n  width: 55rem;\n  height: 39.7rem;\n}\n#task-manager-blur {\n  background-color: var(--bluish-2);\n  backdrop-filter: var(--blur);\n  width: 100%;\n  height: 100%;\n  padding: 1rem;\n}\n#task-manager-inner {\n  display: grid;\n  grid-template-columns: 1fr 3.5fr;\n  grid-template-rows: repeat(auto-fit, minmax(4.5rem, 1fr));\n  width: 100%;\n  height: 100%;\n}\n/* -------------PROJECT PANEL------------------ */\n#project-panel {\n  height: 100%;\n}\n\n#project-panel-header {\n  background-color: var(--bluish-4);\n}\nh3 {\n  font-family: \"Tomorrow-Regular\", \"Courier New\", sans-serif;\n  font-size: 1.6rem;\n  color: var(--brightest);\n}\n.plus-text {\n  font-family: \"Tomorrow-Medium\", \"Courier New\", sans-serif;\n  color: var(--brightest);\n  font-size: 1.7rem;\n  -webkit-transform: rotate(-45deg);\n  -moz-transform: rotate(-45deg);\n  -o-transform: rotate(-45deg);\n  -ms-transform: rotate(-45deg);\n}\n.plus-button {\n  width: 1.6rem;\n  height: 1.6rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--reddish);\n  -webkit-transform: rotate(45deg);\n  -moz-transform: rotate(45deg);\n  -o-transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n  box-shadow: 0rem 0rem 0rem 0.2rem var(--darkest);\n}\n.plus-button:hover {\n  background-color: var(--reddish);\n  -webkit-transform: rotate(90deg);\n  -moz-transform: rotate(90deg);\n  -o-transform: rotate(90deg);\n  -ms-transform: rotate(90deg);\n  .plus-text {\n    -webkit-transform: rotate(-90deg);\n    -moz-transform: rotate(-90deg);\n    -o-transform: rotate(-90deg);\n    -ms-transform: rotate(-90deg);\n  }\n}\n.plus-button:active {\n  scale: 0.8;\n}\n.project-panel-header-cell {\n  width: 100%;\n  height: 3.5rem;\n  padding: 1rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n}\n#project-panel-inner-title-container {\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n#add-project-query {\n  position: absolute;\n  top: 4.2rem;\n\n  left: 7.75rem;\n  width: 5rem;\n}\n#project-list {\n  height: calc(100% - 3.5rem);\n  overflow: scroll;\n}\n\n.project-list-cell {\n  width: 100%;\n  height: 4.5rem;\n  padding: 1rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  flex-direction: column;\n}\n.project-cell-name-container {\n  width: 100%;\n  height: 2rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.project-list {\n  width: 100%;\n  height: 100%;\n}\n\n.drop-arrow {\n  min-width: 2rem;\n  height: 1.4rem;\n  background-image: url(\"../assets/images/drop_down_arrow.webp\");\n  background-size: cover;\n}\n\n@keyframes shake {\n  from {\n    transform: translateY(0rem);\n  }\n  to {\n    transform: translateY(0.15rem);\n  }\n}\n.drop-arrow:hover {\n  animation: 0.2s linear 0s infinite alternate shake;\n}\n.drop-arrow:active {\n  animation-play-state: paused;\n  rotate: 180deg;\n}\n.dark-mode-container {\n  position: relative;\n  right: 4rem;\n  top: 1.5rem;\n  pointer-events: none;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.drop-down-content {\n  visibility: hidden;\n  position: absolute;\n  left: 7.75rem;\n  width: 5rem;\n  transform: translateY(0);\n}\n#dark-mode-list {\n  visibility: hidden;\n  position: relative;\n  left: 0rem;\n  pointer-events: none;\n\n  transform: translateY(0rem);\n}\n#dark-mode-list.visible {\n  visibility: visible;\n  position: relative;\n  left: 0rem;\n  bottom: 1rem;\n  transform: translateY(3rem);\n}\n#dark-mode-arrow {\n  position: relative;\n  width: 2.5rem;\n  height: 1.8rem;\n  top: 2rem;\n  pointer-events: all;\n}\n\n.drop-down-content.visible {\n  visibility: visible;\n  position: absolute;\n  left: 7.75rem;\n  width: 5rem;\n  pointer-events: all;\n  transform: translateY(3rem);\n}\n\n.drop-down-item,\n.drop-down-item:active {\n  background-color: var(--cell-3);\n  pointer-events: all;\n}\n.drop-down-item:nth-child(2n) {\n  background-color: var(--cell-1);\n}\n.drop-down-item:nth-child(2n):active {\n  background-color: var(--darkest);\n}\n.drop-down-item:hover {\n  background-color: var(--darkest);\n  color: var(--brightest);\n}\n\n.drop-down-item,\n.drop-down-item:active {\n  padding: 0.2rem;\n  color: var(--bluish-6);\n}\n\n.project-cell-name {\n  font-family: \"Tomorrow-Regular\", \"Courier New\", sans-serif;\n  color: var(--brightest);\n  width: 7.35rem;\n  height: 1.5rem;\n  white-space: nowrap;\n  overflow-x: scroll;\n  overflow-y: hidden;\n}\n.project-cell-name.today {\n  font-family: \"Tomorrow-Regular\", \"Courier New\", sans-serif;\n  color: var(--brightest);\n  width: 90%;\n  height: 1.5rem;\n  white-space: nowrap;\n  overflow-x: scroll;\n  overf